      />
      <main>
        <section class="title-inputs__wrap">
          <div class="title-box">
            <h4>배송지</h4>
            <v-icon>mdi-chevron-up</v-icon>
          </div>

          <div class="address">
            <p class="mb-0">{{ makeAddr }} / {{ makeMortuary }}</p>
          </div>
        </section>
        <section class="title-inputs__wrap">
          <div class="title-box">
            <h4>받으시는 분</h4>
            <v-icon>mdi-chevron-up</v-icon>
          </div>

          <div class="member-select">
            <select
              id="ribbonRightSelect"
              name="ribbonRightSelect"
              class="ui-select sam2"
              title="받는상주명을 선택해주세요"
              @change="receiveNameSelect"
            >
              <option value="" selected="selected"
                >받는상주명을 선택해주세요</option
              >
              <option value="self">직접입력</option>
              <option
                :value="obituaryMember.obituaryMemberName"
                :number="obituaryMember.obituaryMemberNumber"
                v-for="(obituaryMember,
                index) in getShopObituaryDetail.obituaryObituaryMember"
                :key="index"
                >{{ obituaryMember.obituaryMemberName }}</option
              >
            </select>
            <div style="width: 100%; border-bottom: 1px solid #e6dcd9">
              <v-text-field
                v-if="nameSelfFlag"
                v-model="receive.name"
                type="text"
                name="name"
                dense
                hide-details
                flat
                solo
                style="font-weight: 500; font-size: 14px; color: #8f8f8f"
                required
                placeholder="받는 상주명"
                size="13"
                maxlength="10"
              />
            </div>

            <div style="width: 100%; border-bottom: 1px solid #e6dcd9">
              <v-text-field
                v-model="receive.phone"
                type="tel"
                dense
                hide-details
                flat
                solo
                style="font-weight: 500; font-size: 14px; color: #8f8f8f"
                pattern="[0-9]*"
                name="mobileInp"
                placeholder="받는 상주의 휴대폰번호를 -없이 입력해주세요"
              />
            </div>
            <!-- <v-select
              placeholder="상주선택"
              flat
              solo
              dense
              hide-details
              :items="getShopObituaryDetail.obituaryObituaryMember"
              item-text="obituaryMemberName"
              item-value="obituaryMemberName"
            > 

            </v-select>-->
          </div>
        </section>
        <section class="title-inputs__wrap">
          <div class="title-box">
            <h4>주문하는 분</h4>
            <v-icon>mdi-chevron-up</v-icon>
          </div>
          <div class="px-4 py-3" style="width: 100%">
            <div style="width: 100%; border-bottom: 1px solid #e6dcd9">
              <v-text-field
                v-model="order.name"
                item-text="name"
                item-value="value"
                placeholder="주문자 이름 입력"
                dense
                hide-details
                flat
                solo
                style="font-weight: 500; font-size: 14px; color: #8f8f8f"
              >
              </v-text-field>
            </div>
            <div style="width: 100%; border-bottom: 1px solid #e6dcd9">
              <v-text-field
                v-model="order.phone"
                placeholder="주문자 휴대폰번호를 - 없이 입력해주세요"
                dense
                hide-details
                flat
                solo
                style="font-weight: 500; font-size: 14px; color: #8f8f8f"
              >
              </v-text-field>
            </div>
          </div>
        </section>
        <section class="pa-1 delivery-info" style="">배송 안내</section>
        <section class="title-inputs__wrap">
          <div class="title-box">
            <h4>화환 리본 문구</h4>
            <v-icon small>mdi-help-circle-outline</v-icon>
          </div>
          <div class="px-4 py-3" style="width: 100%">
            <div style="width: 100%; border-bottom: 1px solid #e6dcd9">
              <v-text-field
                v-model="receive.ribbonName1"
                placeholder="예) 장례박사 대표 홍길동"
                dense
                hide-details
                flat
                solo
                style="font-weight: 500; font-size: 14px; color: #8f8f8f"
              >
              </v-text-field>
            </div>
            <div style="width: 100%; border-bottom: 1px solid #e6dcd9">
              <v-select
                v-model="receive.ribbonName2"
                placeholder="삼가 고인의 명복을 빕니다"
                :items="ribbonName2List"
                item-text="text"
                item-value="value"
                dense
                hide-details
                flat
                solo
                style="font-weight: 500; font-size: 14px; color: #8f8f8f"
              >
              </v-select>
            </div>
          </div>
        </section>
        <section class="title-inputs__wrap">
          <div class="title-box">
            <h4>총 결제금액</h4>
            <h4 class="price">{{ getShopDetail.shopPrice }}원</h4>
          </div>

          <div class="price-info__wraps">
            <div class="price-info">
              <p>{{ getShopDetail.shopTitle }}</p>
              <p>{{ getShopDetail.shopPrice }}원</p>
            </div>

            <div class="price-info">
              <p>배송비(도서산간지역 추가 배송비)</p>
              <p>{{ computedDelivery(getShopDetail) }}원</p>
            </div>
          </div>
        </section>
        <section class="title-inputs__wrap">
          <div class="title-box">
            <h4>결제수단</h4>
          </div>

          <div class="button__wraps">
            <button class="active" @click="">신용카드</button>
            <!-- <button @click="">카카오 페이</button> -->
          </div>
        </section>
        <div class="button__wraps mb-4">
          <button @click="requestPay" class="order-button">결제하기</button>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
import ShopFooter from "@/components/common/ShopFooter";
import myMixin from "@/mixin/mixin";
import { mapGetters } from "vuex";
import MobileHeader from "../../components/common/MobileHeader.vue";
export default {
  name: "Shop",
  mixins: [myMixin],
  components: {
    ShopFooter,
    MobileHeader
  },
  data() {
    return {
      receive: {
        name: "",
        phone: null,
        ribbonName1: null,
        addr: null,
        ribbonName2: "삼가 故人의 冥福을 빕니다"
      },
      ribbonName2List: [
        {
          text: "삼가 故人의 冥福을 빕니다",
          value: "삼가 故人의 冥福을 빕니다"
        },
        {
          text: "삼가 고인의 명복을 빕니다",
          value: "삼가 고인의 명복을 빕니다"
        },
        {
          text: "謹 弔",
          value: "謹 弔"
        },
        {
          text: "極樂往生發願",
          value: "極樂往生發願"
        },
        {
          text: "극락왕생발원",
          value: "극락왕생발원"
        },
        {
          text: "천국에서 편히 쉬소서",
          value: "천국에서 편히 쉬소서"
        },
        {
          text: "하나님의 위로가 함께 하시길 빕니다",
          value: "하나님의 위로가 함께 하시길 빕니다"
        },
        {
          text: "주님의 위로와 소망이 함께 하기를 기원합니다",
          value: "주님의 위로와 소망이 함께 하기를 기원합니다"
        }
      ],
      order: {
        name: null,
        phone: null
      },
      nameSelfFlag: false,
      id: this.$route.query.id ? this.$route.query.id : null,
      EcodeId: this.$route.query.EcodeId ? this.$route.query.EcodeId : null,
      url: `${process.env.VUE_APP_BASEURL}/api/pay/payPage`
    };
  },
  mounted() {
    const parameter2 = {
      EcodeId: this.EcodeId
    };
    this.$store.dispatch("REQUEST_OBITUARY_SHOP_INFO", parameter2);
    const parameter = {
      id: this.id,
      EcodeId: this.EcodeId
    };
    this.$store.dispatch("REQUEST_SHOP_DETAIL", parameter);
  },
  computed: {
    ...mapGetters(["getShopDetail", "getShopObituaryDetail"]),
    makeAddr() {
      if (
        this.getShopObituaryDetail.obituaryMember !== undefined &&
        this.getShopObituaryDetail.obituaryMember.memberAttr.codeId === "TYPE-0"
      ) {
        return (
          this.getShopObituaryDetail.obituaryMember.memberAddr +
          "  " +
          this.getShopObituaryDetail.obituaryMember.memberPlaceName
        );
      } else if (
        this.getShopObituaryDetail.obituaryMember !== undefined &&
        this.getShopObituaryDetail.obituaryMember.memberAttr.codeId === "TYPE-1"
      ) {
        return (
          this.getShopObituaryDetail.obituaryAddr +
          "  " +
          this.getShopObituaryDetail.obituaryAddrName
        );
      }
      return "";
    },
    makeMortuary() {
      if (
        this.getShopObituaryDetail.obituaryMember !== undefined &&
        this.getShopObituaryDetail.obituaryMember.memberAttr.codeId === "TYPE-0"
      ) {
        if (
          this.getShopObituaryDetail.obituaryMortuary !== undefined &&
          this.getShopObituaryDetail.obituaryMortuary !== null
        ) {
          return this.getShopObituaryDetail.obituaryMortuary.mortuaryNumber;
        }
      } else if (
        this.getShopObituaryDetail.obituaryMember !== undefined &&
        this.getShopObituaryDetail.obituaryMember.memberAttr.codeId === "TYPE-1"
      ) {
        if (
          this.getShopObituaryDetail.obituaryMortuaryName !== undefined &&
          this.getShopObituaryDetail.obituaryMortuaryName !== null
        ) {
          return this.getShopObituaryDetail.obituaryMortuaryName;
        }
      }

      return "";
    }
  },
  methods: {
    computedDelivery(shop) {
      return shop.deliveryCost;
    },
    computedPrice2(shop) {
      return shop.shopPrice * (1 - shop.shopDc / 100);
    },
    computedPriceAll(shop) {
      return this.computedDelivery(shop) + this.computedPrice2(shop);
    },
    receiveNameSelect(event) {
      if (event.target.value === "self") {
        this.receive.name = "";
        this.nameSelfFlag = true;
      } else {
        this.nameSelfFlag = false;
        this.receive.name = event.target.value;
        for (
          let i = 0;
          i < this.getShopObituaryDetail.obituaryObituaryMember.length;
          i++
        ) {
          if (
            this.getShopObituaryDetail.obituaryObituaryMember[i]
              .obituaryMemberName === event.target.value
          ) {
            this.receive.phone = this.getShopObituaryDetail.obituaryObituaryMember[
              i
            ].obituaryMemberPhone;
          }
        }
      }
    },
    invalidationCheck() {
      if (
        this.receive.ribbonName1 == null ||
        this.receive.ribbonName1 == undefined ||
        this.receive.ribbonName1 === ""
      ) {
        alert("리본문구를 입력해 주세요.");
        return false;
      }

      if (
        this.order.phone == null ||
        this.order.phone == undefined ||
        this.order.phone === ""
      ) {
        alert("주문자 전화번호를 입력하세요.");
        return false;
      }

      return true;
    },
    requestPay() {
      if (!this.invalidationCheck()) {
        return;
      }

      this.receive.addr = this.makeAddr + " " + this.makeMortuary;
      this.receive.obituaryName = this.getShopObituaryDetail.obituaryName;

      const itemList = [
        {
          basketProductCount: 1,
          basketProductType: "SHOP",
          basketMember: null,
          basketShop: { id: this.getShopDetail.id }
        }
      ];

      //PG결제
      const parameter = {
        purchaseType: "SHOP-12-0",
        requestBody: {
          // 2020-07-19, 배송비 추가 필요
          allDeliveryPrice: this.computedDelivery(this.getShopDetail),
          usingSafeNumber: "N",
          deliveryMemoTxt: "",
          usingMemberPoint: 0,
          totalSavings: 0,
          purchaseItemList: itemList,
          purchaseAddressSave: false, // 기존 배송지로할지 안할지
          purchaseReceiveAddress: JSON.stringify(this.receive), //받는사람 주소 json
          purchaseOrderAddress: JSON.stringify(this.order) //보내는 사람 주소 json
        }
      };

      // 2020-07-19, PG 결제 모듈 사용
      const context = this;
      const item = {
        price: this.computedPriceAll(this.getShopDetail),
        subject: this.getShopDetail.shopTitle,
        userId: null,
        userName: this.order.name,
        userPhone: this.order.phone,
        userAddr: this.order.addr,
        parameter: JSON.stringify(parameter)
      };

      var paySubject = encodeURIComponent(item.subject);
      var payUserId = encodeURIComponent(item.userId);
      var payUserName = encodeURIComponent(item.userName);
      var payUserPhone = encodeURIComponent(item.userPhone);
      var payUserAddr = encodeURIComponent(item.userAddr);
      var payParameter = encodeURIComponent(item.parameter);

      window.location.href =
        this.url +
        `?price=${item.price}&subject=${paySubject}&userId=${payUserId}&userName=${payUserName}&userPhone=${payUserPhone}&userAddr=${payUserAddr}&parameter=${payParameter}`;
    },
    back() {
      this.$router.go(-1);
    }
  }
};
</script>
